/*Media query max-width (configuración # 1)*/

/*-------------Formato vertical-------------*/

@media (max-width:525px) {
  
/*Configuración del contenedor padre*/

.contenedor-padre-uno{
  width: 90%;
}

/*Configuración del título del contenedor padre*/

.titulo-cuestionario-uno{
  width: 90%;
}

/*Configuración área para las preguntas*/

.area-pregunta-uno{
  width:90%;
  font-size: 20px;
}

/*Configuración lista desplegable de posibles respuestas*/

.opciones-uno{
  width: 50%;
}

/*Configuración de botones*/

.boton-uno{
  width: 50%;
}

/*Configuración para respuesta del usuario*/

.comprobacion-inicio-uno{
  font-size: 20px;
}

.comprobacion-correcto-uno{
  font-size: 20px;
}

.comprobacion-incorrecto-uno{
  font-size: 20px;
}

}

/*Media query min-width (configuración # 2)*/

/*-------------Formato vertical-------------*/

@media (min-width:525px) {

