}

/*Configuración para respuesta del usuario*/

.comprobacion-inicio-uno{
  font-size: 20px;
}

.comprobacion-correcto-uno{
  font-size: 20px;
}

.comprobacion-incorrecto-uno{
  font-size: 20px;
}

}

/*Media query min-width (configuración # 3)*/

/*-------------Formato vertical-------------*/

@media (min-width:1000px) {

