/*Paleta de colores

#4ABDAC - Verde azul
#DFDCE3 - Gris
#F7B733 - Amarillo
#FC4A1A - Naranja

*/

/*Reinicio de estilos CSS3 para página web*/
*{
    padding: 0;
    margin: 0;
  }

/*Configuración del contenedor padre*/

.contenedor-padre-uno{
  width: 50%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 10px 10px;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  background-color: #DFDCE3;
}

/*Configuración del título del contenedor padre*/

.titulo-cuestionario-uno{
  width: 50%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border: 2px solid #fff;
  font-family: 'Raleway';
  font-size: 25px;
  color: #fff;
  background-color: #FC4A1A;
}

/*Configuración área para las preguntas*/

.area-pregunta-uno{
  margin: 0 auto;
  width:80%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 30px;
  font-family: 'Raleway';
  font-size: 25px;
}

/*Configuración lista desplegable de posibles respuestas*/

.opciones-uno{
  width: 35%;
  font-family: 'Raleway';
  margin: 0 auto;
  margin-bottom: 30px;
}

/*Configuración de botones*/

.contenedor-boton-uno{
  margin: 0 auto;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 40px;
}

.boton-uno{
  width: 20%;
  border: none;
  padding: 15px 15px;
  font-family: 'Raleway';
  background-color: #F7B733;
  border-radius: 5px;
}

/*Configuración para respuesta del usuario*/

.comprobacion-inicio-uno{
  margin-bottom: 20px;
  font-family: 'Raleway';
  text-align: center;
  font-size: 25px;
  color: #000;
}

.comprobacion-correcto-uno{
  margin-bottom: 20px;
  font-family: 'Raleway';
  text-align: center;
  font-size: 25px;
  color: #4ABDAC;
}

.comprobacion-incorrecto-uno{
  margin-bottom: 20px;
  font-family: 'Raleway';
  text-align: center;
  font-size: 25px;
  color: #FC4A1A;
}

/*Configuración score del cliente*/

.score-uno{
  width: 90%;
  margin: 0 auto;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: 'Raleway';
  text-align: center;
}

/*Controlador estados para elementos del cuestionario*/

.invisible{
  display: none;
}