/*Media query max-width (configuración # 1)*/

/*-------------Formato vertical-------------*/

@media (max-width:525px) {
    
  /*Configuración del contenedor padre*/

.contenedor-padre-dos{
    width: 90%;
  }
  
  /*Configuración del título del contenedor padre*/
  
  .titulo-cuestionario-dos{
    width: 90%;
  }

/*Configuración área para las preguntas*/

.area-pregunta-dos{
    margin-top: 15px;
    margin-bottom: 15px;
  }

  /*Configuración lista desplegable de posibles respuestas*/

.opciones-dos{
    width: 50%;
  }

/*Configuración de botones*/

.contenedor-boton-dos{
    margin-bottom: 20px;
}

  .boton-dos{
    width: 50%;
  }

  /*Configuración para respuesta del usuario*/

.comprobacion-inicio-dos{
    font-size: 20px;
  }
  
  .comprobacion-correcto-dos{
    font-size: 20px;
  }
  
  .comprobacion-incorrecto-dos{
    font-size: 20px;
  }

}

/*Media query min-width (configuración # 2)*/

/*-------------Formato vertical-------------*/

@media (min-width:525px) {

 /*Configuración del contenedor padre*/

 .contenedor-padre-dos{
  width: 65%;
}

/*Configuración del título del contenedor padre*/

.titulo-cuestionario-dos{
  width: 65%;
}

/*Configuración mensaje inicial del cuestionario*/

.mensaje-inicial-dos{
  margin-top: 10px;
}

/*Configuración área para las preguntas*/

.area-pregunta-dos{
  margin-top: 10px;
  margin-bottom: 10px;
}

/*Configuración lista desplegable de posibles respuestas*/

.opciones-dos{
  width: 40%;
}

/*Configuración de botones*/

.contenedor-boton-dos{
  margin-bottom: 20px;
}

.boton-dos{
  width: 35%;
}

/*Configuración para respuesta del usuario*/

.comprobacion-inicio-dos{
  font-size: 20px;
}

.comprobacion-correcto-dos{
  font-size: 20px;
}

.comprobacion-incorrecto-dos{
  font-size: 20px;
}

}

/*Media query min-width (configuración # 3)*/

/*-------------Formato vertical-------------*/

@media (min-width:1000px) {

/*Configuración del contenedor padre*/

 .contenedor-padre-dos{
  width: 50%;
}

/*Configuración del título del contenedor padre*/

.titulo-cuestionario-dos{
  width: 50%;
}

/*Configuración lista desplegable de posibles respuestas*/

.opciones-dos{
  width: 30%;
}

/*Configuración de botones*/

.boton-dos{
  width: 30%;
}

}