/*Paleta de colores

#4ABDAC - Verde azul
#DFDCE3 - Gris
#F7B733 - Amarillo
#FC4A1A - Naranja

*/

/*Reinicio de estilos CSS3 para página web*/
*{
  padding: 0;
  margin: 0;
}

/*Configuración del contenedor padre*/

.contenedor-padre-dos{
  width: 50%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 10px 10px;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  background-color: #DFDCE3;
}

/*Configuración del título del contenedor padre*/

.titulo-cuestionario-dos{
  width: 50%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border: 2px solid #fff;
  font-family: 'Raleway';
  font-size: 25px;
  color: #fff;
  background-color: #FC4A1A;
}

/*Configuración mensaje inicial del cuestionario*/

.mensaje-inicial-dos{
  font-family: 'Raleway';
  font-size: 25px;
  color: #FC4A1A;
  margin: 0 auto;
  margin-top: 20px;
}

/*Configuración área para las preguntas*/

.area-pregunta-dos{
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: space-between;
  width:80%;
  margin-top: 30px;
  margin-bottom: 30px;
}

.itemUno{
  width: 20%;
  font-family: 'Raleway';
  font-size: 40px;
  padding-top:15px;
  padding-bottom: 15px;
  text-align: center;
}

.itemDos{
  width: 20%;
  font-family: 'Raleway';
  font-size: 40px;
  padding-top:15px;
  padding-bottom: 15px;
  text-align: center;
}

.itemTres{
  width: 20%;
  font-family: 'Raleway';
  font-size: 40px;
  padding-top:15px;
  padding-bottom: 15px;
  text-align: center;
}

/*Configuración lista desplegable de posibles respuestas*/

.opciones-dos{
  width: 35%;
  font-family: 'Raleway';
  margin: 0 auto;
  margin-bottom: 30px;
}

/*Configuración de botones*/

.contenedor-boton-dos{
  margin: 0 auto;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 40px;
}

.boton-dos{
  width: 20%;
  border: none;
  padding: 15px 15px;
  font-family: 'Raleway';
  background-color: #F7B733;
  border-radius: 5px;
}

/*Configuración para respuesta del usuario*/

.comprobacion-inicio-dos{
  margin-bottom: 20px;
  font-family: 'Raleway';
  text-align: center;
  font-size: 25px;
  color: rgb(0, 0, 0);
}

.comprobacion-correcto-dos{
  margin-bottom: 20px;
  font-family: 'Raleway';
  text-align: center;
  font-size: 25px;
  color: #4ABDAC;
}

.comprobacion-incorrecto-dos{
  margin-bottom: 20px;
  font-family: 'Raleway';
  text-align: center;
  font-size: 25px;
  color: #FC4A1A;
}

/*Configuración score del cliente*/

.score-dos{
  width: 90%;
  margin: 0 auto;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: 'Raleway';
  text-align: center;
}

/*Controlador estados para elementos del cuestionario*/

.invisible{
  display: none;
}